.footer {
  width: 100%;
  background-image: url(/src/images/bike-footer.png);
  background-size: cover;
  background-position: center;
  position: relative;
  z-index: -1;


  @include on-tablet {
    height: 480px;
    background-attachment: fixed;

  }

  @include on-desktop {
    height: 555px;
  }
}
