.section-title {
  font-size: 32px;
  line-height: 1;
  font-weight: 500;
  letter-spacing: 1.5px;
  text-align: center;
  margin: 0 0 48px;

  &--align-left {
    text-align: left;
    white-space: nowrap;
  }

  @include on-tablet {
    font-size: 48px;
  }

  @include on-desktop {
    font-size: 64px;
  }
}
